<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-9 col-md-7 col-lg-5 mx-auto">
        <div class="card card-signup my-5">
          <div class="card-body">
            <h5 class="card-title text-center">Sign Up</h5>
            <form class="form-signup" v-on:submit.prevent="onSubmit(registerData);">
              <div class="form-label-group">
                <input
                  type="text"
                  id="inputFirstName"
                  class="form-control"
                  placeholder="First name"
                  v-model="registerData.firstName"
                  required
                  autofocus
                >
                <label for="inputFirstName">First name</label>
              </div>
              <div class="form-label-group">
                <input
                  type="text"
                  id="inputLastName"
                  class="form-control"
                  placeholder="Last name"
                  v-model="registerData.lastName"
                  required
                >
                <label for="inputLastName">Last name</label>
              </div>
              <div class="form-label-group">
                <input
                  type="text"
                  id="inputUser"
                  class="form-control"
                  placeholder="User name"
                  v-model="registerData.username"
                  required
                >
                <label for="inputUser">Email address</label>
              </div>

              <div class="form-label-group">
                <input
                  type="password"
                  id="inputPassword"
                  class="form-control"
                  placeholder="Password"
                  v-model="registerData.password"
                  required
                >
                <label for="inputPassword">Password</label>
              </div>

              <div class="custom-control custom-checkbox mb-3">
                <input type="checkbox" class="custom-control-input" id="customCheck1">
                <label class="custom-control-label" for="customCheck1">Remember password</label>
              </div>
              <button class="btn btn-lg btn-primary btn-block text-uppercase" type="submit">Sign Up</button>
              <div class="form-label-group sign-up">
                <label>
                  Have an account?
                  <a href="#/login">Login</a>
                </label>
              </div>
              <div class="go-home">
                <a href="#/">
                  <i class="fas fa-home"></i>
                </a>
                <!-- <router-link :to="login"> -->
                <!-- <router-link class="nav-link" to="/home">
                  <i class="fas fa-home"></i>
                </router-link>-->

                <!-- </router-link> -->
              </div>

              <!-- <label>
                Have an account?
                <a href="#/login">Login</a>
              </label>-->
              <!-- <hr class="my-4">
              <button class="btn btn-lg btn-google btn-block text-uppercase" type="submit">
                <i class="fab fa-google mr-2"></i> Sign in with Google
              </button>
              <button class="btn btn-lg btn-facebook btn-block text-uppercase" type="submit">
                <i class="fab fa-facebook-f mr-2"></i> Sign in with Facebook
              </button>-->
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { APIService } from "../common/APIService";
const apiService = new APIService();
export default {
  data() {
    return {
      registerData: {
        firstName: null,
        lastName: null,
        username: null,
        password: null
      },
      posts: null
    };
  },
  methods: {
    onSubmit: function(formData) {
      var that = this;
      apiService.register(formData).then(
        function(res) {
          that.$router.push("/login");
        },
        function(err) {
          console.log(err.response.data.message);
        }
      );
    }
  }
};
</script>

<style scopped>
:root {
  --input-padding-x: 1.5rem;
  --input-padding-y: 0.75rem;
}

.card-signup {
  border: 1px solid #eeecec;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
}

.card-signup .card-title {
  margin-bottom: 2rem;
  font-weight: 300;
  font-size: 1.5rem;
}

.card-signup .card-body {
  padding: 2rem;
}

.form-signup {
  width: 100%;
}

.form-signup .btn {
  font-size: 80%;
  border-radius: 5rem;
  letter-spacing: 0.1rem;
  font-weight: bold;
  padding: 1rem;
  transition: all 0.2s;
}

.form-label-group {
  position: relative;
  margin-bottom: 1rem;
}

.form-label-group input {
  height: auto;
  border-radius: 2rem;
}

.form-label-group > input,
.form-label-group > label {
  padding: var(--input-padding-y) var(--input-padding-x);
}

.form-label-group > label {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  margin-bottom: 0;
  /* Override default `<label>` margin */
  line-height: 1.5;
  color: #495057;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  transition: all 0.1s ease-in-out;
}

.form-label-group input::-webkit-input-placeholder {
  color: transparent;
}

.form-label-group input:-ms-input-placeholder {
  color: transparent;
}

.form-label-group input::-ms-input-placeholder {
  color: transparent;
}

.form-label-group input::-moz-placeholder {
  color: transparent;
}

.form-label-group input::placeholder {
  color: transparent;
}

.form-label-group input:not(:placeholder-shown) {
  padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));
  padding-bottom: calc(var(--input-padding-y) / 3);
}

.form-label-group input:not(:placeholder-shown) ~ label {
  padding-top: calc(var(--input-padding-y) / 3);
  padding-bottom: calc(var(--input-padding-y) / 3);
  font-size: 12px;
  color: #777;
}

.btn-google {
  color: white;
  background-color: #ea4335;
}

.btn-facebook {
  color: white;
  background-color: #3b5998;
}

.sign-up {
  text-align: center;
  padding-bottom: 40px;
}
.go-home {
  text-align: center;
  cursor: pointer;
}
</style>



